
const { ccclass, property } = cc._decorator;

@ccclass
export default class NewClass extends cc.Component {

    rigidBody: cc.RigidBody;

    @property
    force: cc.Vec2 = new cc.Vec2(0, 20);

    onLoad() {

        cc.systemEvent.on(cc.SystemEvent.EventType.KEY_DOWN, this.onKeyDown, this);
        cc.systemEvent.on(cc.SystemEvent.EventType.KEY_UP, this.onKeyUp, this);

        this.rigidBody = this.getComponent(cc.RigidBody);

    }

    onKeyDown(event) {
        switch (event.keyCode) {
            case cc.macro.KEY.up:
                console.log('Press a key');
                // this.rigidBody.angularVelocity = 100;
                this.rigidBody.linearVelocity = new cc.Vec2(0, 100);
                break;
            case cc.macro.KEY.down:
                console.log('Press a key');
                break;
            case cc.macro.KEY.right:
                console.log('Press right key');
                this.applyForceToShip(this.node.convertToWorldSpaceAR(this.node.getChildByName('leftEngine').position));
                break;
            case cc.macro.KEY.left:
                console.log('Press left key');
                this.applyForceToShip(this.node.convertToWorldSpaceAR(this.node.getChildByName('rightEngine').position));
                break;
        }
    }

    onKeyUp(event) {
        switch (event.keyCode) {
            case cc.macro.KEY.up:
                console.log('Press a key');
                break;
            case cc.macro.KEY.down:
                console.log('Press a key');
                break;
            case cc.macro.KEY.right:
                console.log('Press a key');
                break;
            case cc.macro.KEY.left:
                console.log('Press a key');
                break;
        }
    }

    applyForceToShip(point: cc.Vec3) {
        cc.log(point);
        this.rigidBody.applyForce(this.force, new cc.Vec2(point.x, point.y), true);
    }

    start() {
        // this.rigidBody.fixedRotation = true;
        this.rigidBody.enabledContactListener = true;
    }

    update(dt) {

    }

    onDestroy() {
        // Cancel keyboard input monitoring
        cc.systemEvent.off(cc.SystemEvent.EventType.KEY_DOWN, this.onKeyDown, this);
        cc.systemEvent.off(cc.SystemEvent.EventType.KEY_UP, this.onKeyUp, this);
    }
}
